import { useEffect } from "react";
import LoginBox from "../../components/LoginBox";

function Login() {
  useEffect(() => {
    const root = document.getElementById("root");

    root.classList.add("login");

    let script = document.createElement("script");

    script.append(
      "function login_fail(){let button = document.querySelector('.login-button'); button.classList.add('.active');} window.login_fail = login_fail;"
    );

    let body = document.body;
    body.append(script);

    return function cleanClass() {
      root.classList.remove("login");
    };
  });

  return (
    <form
      action="https://admin.wingstation.co.kr/login/"
      className="loginMainBox"
      method="POST"
      target="login"
    >
      <LoginBox />
      <iframe
        style={{ display: "none" }}
        title="loginPage"
        name="login"
      ></iframe>
    </form>
  );
}

export default Login;
