import React from "react";
import LoginInput from "./LoginInput";
import LoginButton from "./LoginButton";
import logo from "../images/login_logo.svg";
import questionLogo from "../images/question.png";

class Head extends React.Component {
  render() {
    return (
      <div className="head">
        <img src={logo} alt="로고 이미지"></img>
        <h1 className="head-text">CONTROL SYSTEM</h1>
      </div>
    );
  }
}

class MessageBox extends React.Component {
  render() {
    return (
      <div
        className={this.props.clicked ? "messageBox pullDown" : "messageBox"}
      >
        <div>{this.props.text}</div>
        <div className="loginQuestion-container">
          <img
            src={questionLogo}
            alt="questionLogo"
            style={{ marginLeft: "1px" }}
          ></img>
          <div
            style={{ position: "relative", marginLeft: "2px", bottom: "1px" }}
          >
            1600-2834
          </div>
        </div>
      </div>
    );
  }
}

class LoginBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      clicked: 0,
      text: "미가입 아이디이거나, 잘못된 비밀번호입니다.",
    };
  }

  render() {
    return (
      <>
        <Head />
        <div className="loginBox">
          <LoginInput name="id" type="text" placeholder="ID" />
          <LoginInput name="pw" type="password" placeholder="PASSWORD" />
          <MessageBox clicked={this.state.clicked} text={this.state.text} />
          <LoginButton text="로그인" />
        </div>
      </>
    );
  }
}

export default LoginBox;
